.footer{background-color:#161819;color:#136b1f;position:relative;z-index:10}.footer__container{min-height:8vh}.footer img{height:70%;width:70%}

.header{background-color:#161819;font-family:"Zen Dots",cursive;position:relative;z-index:10}.header__container{min-height:10vh}.header__container ul{padding:0;margin:0}.header__container li{display:inline;padding:0 .9375rem}.header__container li a{font-size:.75rem;color:#136b1f}

.contact{background-color:#121113;position:relative}.contact__content{margin-top:3.125rem}.contact .mask{position:absolute;bottom:0;right:34%;opacity:30% !important}.contact .section{min-height:82vh}.contact .section .title{color:#136b1f;font-size:1.875rem;font-family:"Zen Dots",cursive}.contact .section .paragraph{font-size:1.25rem;color:white;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.contact .section .pcontact{margin:0 0 2.5rem 0}.contact .section .pcontact i{font-size:2.0625rem;color:#136b1f;margin-right:2.5rem}.contact .section .pcontact a{color:#fff}

.myform label,.myform input,.myform textarea{color:#fff;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.myform label{font-size:1.25rem}.myform input.form-control,.myform textarea{background-color:transparent;border-radius:0;border:none;border-bottom:solid .0625rem #136b1f}.myform textarea:focus{background-color:transparent}.myform button{font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300;background-color:#136b1f;border:none;width:6.1875rem}.myform button:hover{background-color:#136b1f}

.home{background-color:#121113;position:relative}.home .mask{position:absolute;opacity:30%;right:0;height:100% !important}.home .section{min-height:82vh}.home .section div{max-width:56.625rem}.home .section .title{color:#136b1f;font-size:1.875rem;font-family:"Zen Dots",cursive}.home .section .subtitle{font-size:1.5625rem;color:#fff;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:400}.home .section .paragraph{font-size:1.25rem;color:#fff;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}

.myproyects{background-color:#121113}.myproyects .container{position:relative}.myproyects .container .mask{position:absolute;bottom:0;opacity:30% !important}.myproyects .section{min-height:82vh;margin:3.125rem 0 3.125rem 0}.myproyects__title{text-align:center;color:#136b1f;font-family:"Zen Dots",cursive;font-size:1.875rem}.myproyects__title p{margin:0;color:white;font-size:1.25rem;margin-bottom:3.125rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.myproyects__list{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}

.card{border:solid .0625rem #136b1f;margin-top:1.5625rem;box-shadow:0 .25rem .25rem rgba(0,0,0,0.25);border-radius:.9375rem}.card .moreinfo{display:none !important;transition:0.4s}.card .card-image{max-height:11.1875rem;border-radius:.9375rem .9375rem 0 0}.card .card-body{border-radius:0 0 .9375rem .9375rem;height:9.6875rem;background-color:#313131;color:#fff;text-align:center}.card .card-body .card-title{font-size:1.25rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:600}.card .card-body .card-description{font-size:.8125rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.card:hover .moreinfo{display:-webkit-inline-flex !important;display:inline-flex !important;background-color:#121113a8;height:11.1875rem;position:absolute}.card:hover .moreinfo a,.card:hover .moreinfo p,.card:hover .moreinfo i{color:white}.card:hover .moreinfo a,.card:hover .moreinfo p{font-size:.9375rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.card:hover .moreinfo i{font-size:1.875rem}.card:hover .moreinfo i.like{color:#136b1f}.card:hover .moreinfo .dislike{color:grey}

.singleproyect{background-color:#121113;position:relative}.singleproyect .mask{position:absolute;right:0;bottom:0;opacity:30% !important}.singleproyect .section{min-height:82vh;margin:3.125rem 0}.singleproyect .section h2.title{font-size:1.875rem;color:#136b1f;font-family:"Zen Dots",cursive}.singleproyect .section p.description{color:#fff;font-size:1.25rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.singleproyect .section li{color:#fff;font-size:1.25rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.singleproyect .section .slider{margin:3.125rem 0}.singleproyect .section .subtitle{color:#136b1f;font-size:1.25rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:700}.singleproyect .section .subtitle a{color:#fff;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.singleproyect .section .work{color:#fff;font-size:1.25rem;font-family:"Encode Sans Semi Expanded",sans-serif;font-weight:300}.singleproyect .section #slider .swiper-button-next{background-image:url(data:image/png;base64,);height:3.5625rem;width:3.5625rem}.singleproyect .section #slider .swiper-button-next::after{display:none}.singleproyect .section #slider .swiper-button-prev{background-image:url(data:image/png;base64,);height:3.5625rem;width:3.5625rem}.singleproyect .section #slider .swiper-button-prev::after{display:none}.singleproyect .section #slider .swiper-pagination-bullet{background-color:#fff;opacity:unset;height:.625rem;width:.625rem}.singleproyect .section #slider .swiper-pagination-bullet-active{background-color:#136b1f}.singleproyect .section #slider .slide-img{width:100%}

